
@font-face {
  font-family: Montserrat;
  src: url(./fonts/Montserrat-Regular.ttf);
}
body{
    margin : 0px;
    background-color : #dddddd;
    color: #333333;
}
html, body, #root{
    height: 100%;
}

*{
    font-family: Montserrat;   
    box-sizing: border-box;
}
.title{
    display: inline-block;
    padding-left: 48px;
    font-size: 50px;
    float: left;
    padding-top: 16px;
    height: 100%;
    width: 344px;
    text-align: left;
    color: #ff7700;
    margin: 0px;
}
header{
    background-color: #333333;
    display: inline-block;
    width: 100%;
    height : 100px;
    color : #dddddd;
    border-bottom : 10px solid #dddddd;
}

.menu{
    display: inline-block;
    padding: 0px 0px;
    margin: 0px;
    position: absolute;
    top: 0px;
    right: 100px;
    width: 100px;
    background-color: #d5a04b;
}
.menu__option{
    display: inline-block;
    padding: 40px 0px;
    text-align: center;
    height: 100px;
    width: 100%;
    cursor: pointer;
    color: black;
    list-style-type: none;
}

.menu__option:hover{
    background-color: #a02423;
    color: white;
}

.main-content{
    display: inline-block;
    width: 100%;
    position: relative;
    background: #dddddd;
    height: fit-content;
    min-height: 100%;
}
.tab{
    margin: 0px 20%;
    padding: 10px 0px;
    text-align: center;
}
.title{
    font-size: 35px;
    text-align: center;
    width: 70%;
    padding: 15px 0px;
    margin: 10px 15%;
    color: #676767;
    border-bottom: 1px solid lightgray;
}
.secondary-title{
    margin-top: 11px;
    font-size: 23px;
    color: #929191;
}
.fixed{
    top: 50px;
    position: fixed;
}

.yellow-background{
    background-color: #fef3b2;
}
.red-background{
    background-color: #a02423;
}
.green-background{
    background-color: green;
}