

.image-block-container{
    height: 350px;
    width: 60%;
    margin: auto;
}
.face-image{
    border-radius: 200px;
    float: left;
}
.basic-info-container{
    display: inline-block;
    width: calc( 100% - 200px );
    float: left;
}
.basic-info__unit{
    padding: 20px 10px;
}
.basic-info__name{
    display: inline-block;
    width: 50%;
    float: left;
}
.basic-info__val{
    display: inline-block;
    width: 50%;
    float: left;
}
.story-container{
    text-align: justify;
    width: 60%;
    margin: 20px auto 44px;
}