@font-face{
    font-family: narutoFont;
    src : url("../fonts/njnaruto.ttf");
}

#home-tab{
    width: 100%;
    height: 100%;
    background: url(../images/naruto-minimal-wallpaper.png) no-repeat center center fixed;
    background-repeat: no-repeat;
    background-color: black;
    position: relative;
    text-align: center;
}
.home-title{
    display: inline-block;
    width: 100%;
    min-width: 300px;
    font-family: narutoFont;

}
#home-title-container{
    display: inline-block;
    width: 100%;
    height: 100%;
    margin: 10% 0px;
    left: 0px;
    position: absolute;
}